// 该文件专门用于创建整个应用的路由器
import VueRouter from 'vue-router'
//引入组件


import Home from '../views/Home.vue'
import Types from '../views/Types.vue'
import Archives from '../views/Archives.vue'
import About from '../views/About.vue'
import Blog from '../views/Blog.vue'
import Login from '../views/Login.vue'
import PostBlogs from '../views/admin/PostBlogs.vue'




import adminHome from '../views/admin/adminHome.vue'
import Blogs from '../views/admin/Blogs.vue'
import Types2 from '../views/admin/Types2.vue'
import Report from '../views/admin/Report.vue'
import Report2 from '../views/admin/Report2.vue'
import Users from '../views/admin/Users.vue'
import Comments from '../views/admin/Comments.vue'




//创建并暴露一个路由器
export default new VueRouter({
	//***************  服务器的必要代码--添加这两行代码
	// mode: 'hash',
	
// ****************************************************************
	routes:[

		{	path:'/',
			redirect:'/home'
		},
		{
			path:'/home',
			component:Home
		},
		{
			path: '/Types',
			name: 'Types',
			component: Types
		},
		{
			path: '/Archives',
			name: 'Archives',
			component: Archives
		},
		{
		path: '/About',
		name: 'About',
		component: About
		},

		
		
		{
		path: '/Blog',
		name: 'Blog',
		component: Blog
		},
		{
			path: '/Login',
			component:Login
		},
		{
			path: '/adminHome',
			component: adminHome,
			
			children: [ 
		
				{ path: 'PostBlogs', component: PostBlogs },
				{ path: 'Blogs', component:Blogs},
				{ path: 'Types2', component: Types2 },
				{ path: 'report', component: Report },
				{ path: 'report2', component: Report2 },
				{ path: 'Users', component: Users },
				{ path: 'Comments', component: Comments }
			
	
			]
		}

		
		
	]
})
